import { useState, useEffect } from "react";
import { MDBFooter } from "mdb-react-ui-kit";
function ViewReport() {
    const [reportData, setReportData] = useState([]);
    useEffect(() => {
        async function fetchData() {
            try {
                const response = await fetch(
                    "https://65b4d71441db5efd2867019c.mockapi.io/data/1"
                );
                if (!response.ok) {
                    throw new Error("Network response was not ok");
                }
                const data = await response.json();
                setReportData(data);
            } catch (error) {
                console.error("Error fetching data:", error);
            }
        }

        fetchData();
    }, []);

    return (
        <>
            <h1>
                <center>
                    Bo co messenger
                </center>
            </h1>
            <div className="row ms-5 mt-3 me-5">
                <div className="col-6">
                    <textarea name="" id="" cols="70" rows="10" value={`
HP99 SG ${reportData.date}
** Sng
Nhp: ${reportData.total}
** Doanh s 
Xut : ${reportData.tron_b + reportData.em_b + reportData.dam_b + reportData.scl_b + reportData.tron_t + reportData.em_t + reportData.dam_t + reportData.scl_t + reportData.tron_5 + reportData.em_5 + reportData.dam_5 + reportData.scl_5 +
                        reportData.tron_sp + reportData.em_sp + reportData.dam_sp + reportData.scl_sp +
                        reportData.tron_gr + reportData.em_gr + reportData.dam_gr + reportData.scl_gr +
                        reportData.tron_f + reportData.em_f + reportData.dam_f + reportData.scl_f} chai
    - Bn : ${reportData.tron_b + reportData.em_b + reportData.dam_b + reportData.scl_b} chai
    - Tng : ${reportData.tron_t + reportData.em_t + reportData.dam_t + reportData.scl_t} chai
    - Phiu 5K :${reportData.tron_5 + reportData.em_5 + reportData.dam_5 + reportData.scl_5} chai
    - Shoppe : ${reportData.tron_sp + reportData.em_sp + reportData.dam_sp + reportData.scl_sp} chai
    - Grab : ${reportData.tron_gr + reportData.em_gr + reportData.dam_gr + reportData.scl_gr} chai
    - Free : ${reportData.tron_f + reportData.em_f + reportData.dam_f + reportData.scl_f} chai
** Doanh thu 
    - Bn : ${(reportData.tron_b + reportData.em_b + reportData.dam_b + reportData.scl_b) * 27}K
    - Phiu 5K : ${(reportData.tron_5 + reportData.em_5 + reportData.dam_5 + reportData.scl_5) * 5}K
    - Shoppe : ${reportData.sp}K
    - Grab : ${reportData.gr}K
    - Chi khc : ${reportData.chi_khac}K
    - CN LI : ${(reportData.tron_b + reportData.em_b + reportData.dam_b + reportData.scl_b) * 27 + (reportData.tron_5 + reportData.em_5 + reportData.dam_5 + reportData.scl_5) * 5 + reportData.sp + reportData.gr - reportData.chi_khac}K
`}>
                    </textarea>
                </div>
                <div className="col-6">
                    <textarea name="" id="" cols="70" rows="10" value={`
HP99 SG ${reportData.date}
** Nguyn liu : 
    - Bch combo : ${reportData.bich_combo}
    - Ly giy : ${reportData.ly_giay}
    - Np : ${reportData.nap}
    - ng ht : ${reportData.ong_hut}
    - Ch T : ${reportData.chu_t}
    - Bch rc : ${reportData.bich_rac}
                    `}></textarea>
                </div>
            </div>
            <div className="row ms-5 me-5 mt-5">
                <div className="col-6">
                    <textarea name="" id="" cols="70" rows="10" value={`
Tn 
    - Trn v : ${reportData.total_tron} : ${reportData.total_tron - reportData.tron_b - reportData.tron_t - reportData.tron_5 - reportData.tron_sp - reportData.tron_gr - reportData.tron_f} 
    - m du : ${reportData.total_em} : ${reportData.total_em - reportData.em_b - reportData.em_t - reportData.em_5 - reportData.em_sp - reportData.em_gr - reportData.em_f}
    - m  :  ${reportData.total_dam} : ${reportData.total_dam - reportData.dam_b - reportData.dam_t - reportData.dam_5 - reportData.dam_sp - reportData.dam_gr - reportData.dam_f}
    - Quyn r :  ${reportData.total_scl} : ${reportData.total_scl - reportData.scl_b - reportData.scl_t - reportData.scl_5 - reportData.scl_sp - reportData.scl_gr - reportData.scl_f}
                    `}></textarea>
                </div>
            </div>
            <MDBFooter bgColor='light' className='text-center text-lg-left'>
                <div className='text-center p-3' style={{ backgroundColor: 'rgba(0, 0, 0, 0.2)' }}>
                    &copy; {new Date().getFullYear()} Copyright by Minh Quan
                </div>
            </MDBFooter>
        </>
    );
}

export default ViewReport;